import React from 'react'
import Layout from '../components/layout/Layout';
import {useState,useEffect} from 'react';

const HomePage = () => {

  const [val,setVal] = useState("");
  
  useEffect(()=>{

    const data = localStorage.getItem("user"); 
    
    setVal(data.name)

  })


  return (

   <Layout>
     <h1>home Page</h1>
   </Layout>
   
  )
}

export default HomePage